
.avatar-uploader {
	display: inline-block;
	margin-right: 10px;
}
.avatar-uploader .el-upload {
	border: 1px dashed #d9d9d9;
	border-radius: 6px;
	cursor: pointer;
	width: 160px;
	height: 77px;
	position: relative;
	overflow: hidden;
}
.avatar-uploader .el-upload:hover {
	border-color: #409EFF;
}
.avatar-uploader-icon {
	font-size: 32px;
	color: #8c939d;
	width: 160px;
	height: 100px;
	line-height: 100px;
	text-align: center;
}
.pictext {
	display: inline-block;
	margin-top: 18px;
	font-size: 13px;
	color: #8c939d;
	line-height: 20px;
}
.upload-pic {
	display: inline-table;
}
.avatar {
	display: inline-block;
	margin-right: 10px;
	width: 160px;
	height: 77px;
}
.del {
	position: absolute;
	z-index: 99;
	left: 8px;
	top: 8px;
	font-size: 12px;
	color: #000;
	cursor: pointer;
}
.xig {
	color: #000; 
	float: left;
	margin: 0 0 0-20px;
	z-index: 99;
	position: absolute;
	left: 180px;
	display: block;
	height: 40px;
	width: 60px;
	line-height: 40px;
	cursor: pointer;
	top: 0;
}



